// pages/index.js
import { useEffect, useState } from "react";
import axios from "axios"; // if you plan to fetch real data later

export default function Home({ initialProducts }) {
  const [products, setProducts] = useState(initialProducts || []); // Fallback to empty array
  const [cart, setCart] = useState({}); // { productId: quantity }

  const addToCart = (productId) => {
    setCart((prev) => {
      const qty = prev[productId] || 0;
      return { ...prev, [productId]: qty + 1 };
    });
  };

  const updateQuantity = (productId, quantity) => {
    setCart((prev) => {
      if (quantity <= 0) {
        const newCart = { ...prev };
        delete newCart[productId]; // Remove item if quantity is zero or negative
        return newCart;
      }
      return { ...prev, [productId]: quantity };
    });
  };

  const totalItems = Object.values(cart).reduce((sum, q) => sum + q, 0);
  const totalPrice = Object.entries(cart).reduce((sum, [id, qty]) => {
    const product = products.find((p) => p.id === parseInt(id));
    return sum + (product?.price || 0) * qty;
  }, 0);

  return (
    <div className="bg-gray-100 min-h-screen font-sans text-gray-900">
      <header className="flex justify-between items-center bg-blue-600 text-white p-4">
        <h1 className="text-3xl font-bold">Shop</h1>
        <div className="text-lg">
          ðŸ›’ {totalItems} items (${totalPrice.toFixed(2)})
        </div>
      </header>

      <div className="container mx-auto p-6">
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-8">
          {products.length === 0 ? (
            <p className="text-center text-xl">No products available</p>
          ) : (
            products.map((product) => (
              <div
                key={product.id}
                className="bg-white p-4 rounded-lg shadow-lg hover:shadow-2xl transition-all"
              >
                <h2 className="text-xl font-semibold">{product.name}</h2>
                <p className="text-lg text-blue-600">${product.price}</p>
                <div className="flex flex-col gap-3 mt-4">
                  <button
                    className="bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 transition-colors"
                    onClick={() => addToCart(product.id)}
                  >
                    Add to Cart
                  </button>
                  {cart[product.id] && (
                    <div className="flex items-center gap-2">
                      <label htmlFor={`quantity-${product.id}`} className="text-sm">
                        Quantity:
                      </label>
                      <input
                        id={`quantity-${product.id}`}
                        type="number"
                        value={cart[product.id]}
                        onChange={(e) => updateQuantity(product.id, parseInt(e.target.value) || 0)}
                        className="w-16 border px-2 py-1 rounded-md text-center"
                      />
                    </div>
                  )}
                </div>
              </div>
            ))
          )}
        </div>
      </div>
    </div>
  );
}

export async function getServerSideProps() {
  // Mock product data
  const mockProducts = [
    { id: 1, name: 'T-Shirt', price: 20 },
    { id: 2, name: 'Shoes', price: 50 },
    { id: 3, name: 'Hat', price: 15 },
  ];

  const products = mockProducts || [];
  return {
    props: { initialProducts: products },
  };
}
